.component {
  width: 100%;

  & .generate_btn {
    user-select: none;
    position: relative;
    width: 100%;
    height: 50px;
    margin: 10px 0;
    border-radius: 8px;
    color: #fff;
    border: none;
    background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    letter-spacing: 1px;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
    transition: all 150ms ease;
    &:active {
      transform: translateY(-3%);
      box-shadow: 0 4px 8px rgba(255, 255, 255, 0.08);
    }
  }
}
